import React from "react";
import { Button, Form, FormControl, Image, Modal } from "react-bootstrap";
import { useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";

//
// setnewImg
// 
// 
//newImg()
function Create({ onHide, show }) {
  const [status, setstatus] = useState(1); //status
  const [name, setname] = useState(""); //oname->oldname
  const [address, setaddress] = useState("");
  const [phone, setphone] = useState("");
  const [city, setcity] = useState("");
  const [cost, setcost] = useState("");
  const [ft, setft] = useState("");
  const [f1, setf1] = useState("11:00");
  const [f2, setf2] = useState("14:00");
  const [s1, sets1] = useState("");
  const [s2, sets2] = useState("");
  const [tag1, settag1] = useState("");
  const [tag2, settag2] = useState("");
  const [tag3, settag3] = useState("");
  const [article, setarticle] = useState("");
  const [files, setFiles] = useState([]); //FileList{0: File, length: 1}//1.post2.

  //
  let img = "";
  const inputImg = (e) => {
    setFiles(e.target.files);
    let blah = document.getElementById("blah");
    let [file] = e.target.files;
    if (file) {
      blah.src = URL.createObjectURL(file);
    }
    //[file]createurlid=blah
  };

  // put()
  const onSubmit = async () => {
    axios
      .post("http://localhost:3001/sdata/create", {
        name: name,
        status: status,
        address: address,
        phone: phone,
        city: city,
        ft: ft,
        cost: cost,
        tag1: tag1,
        tag2: tag2,
        tag3: tag3,
        img: img,
        time1: f1 + "-" + f2,
        time2: s1 + "-" + s2,
        article: article,
      })
      .then((res) => {
        toast.success("");

        window.location.reload();
      })
      .catch((error) => {
        console.error(error);
      });
  };
  //post
  const onSubmitF = async (e) => {
    e.preventDefault();
    const data = new FormData();

    for (let i = 0; i < files.length; i++) {
      data.append("file", files[i]);
    } //data[] key'file' ,post

    await axios //
      .post("http://localhost:3001/sdata/picupdate", data)
      .then((response) => {
        console.log(response.data[0].filename); //
        img = response.data[0].filename;
      })
      .then(() => {})
      .catch((e) => {
        toast.error("Upload Error");
      });
  };

  return (
    <Modal
      className="model "
      size="lg"
      aria-labelledby="contained-modal-title-vcenter "
      centered
      {...{ onHide, show }}
    >
      <Form className=" mx-auto frame my-5">
        <h5 className="line">
          
          <button
            className="buttonStyle"
            style={{
              fontSize: "15px",
              float: "right",
              borderRadius: "3px 3px 3px 3px",
              // marginTop: "-20px",
              // marginRight: "-10px",
            }}
            // className="btn-close"
          >
            CLOSE
          </button>
        </h5>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="name"
            type="text"
            placeholder="name"
            value={name}
            onChange={(e) => {
              setname(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="ft"
            id="ft"
            value={status}
            onChange={(e) => {
              setstatus(e.target.value);
            }}
          >
            <option value={1}></option>
            <option value={0}></option>
          </Form.Select>
        </Form.Group>
        <Form.Label></Form.Label>
        <FormControl
          id="impInp"
          type="file"
          onChange={(e) => inputImg(e)}
          className="form-control"
          multiple
          accept="image/gif, image/jpeg, image/png"
        ></FormControl>

        <Image
          id="blah"
          src={img && `http://localhost:3001/${img}`}
          thumbnail
        />

        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="address"
            type="text"
            placeholder="address"
            value={address}
            onChange={(e) => {
              setaddress(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="phone"
            type="text"
            placeholder="phone"
            value={phone}
            onChange={(e) => {
              setphone(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="city"
            id="city"
            value={city}
            onChange={(e) => {
              setcity(e.target.value);
            }}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="ft"
            id="ft"
            value={ft}
            onChange={(e) => {
              setft(e.target.value);
            }}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="cost"
            type="text"
            placeholder="$"
            value={cost}
            onChange={(e) => {
              setcost(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>1</Form.Label>
          <FormControl
            style={{ marginBottom: "5px" }}
            type="time"
            value={f1}
            onChange={(e) => {
              setf1(e.target.value);
            }}
          />
          <FormControl
            type="time"
            value={f2}
            onChange={(e) => {
              setf2(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>2</Form.Label>
          <FormControl
            style={{ marginBottom: "5px" }}
            type="time"
            value={s1}
            onChange={(e) => {
              sets1(e.target.value);
            }}
          />
          <FormControl
            type="time"
            value={s2}
            onChange={(e) => {
              sets2(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>1</Form.Label>
          <FormControl
            id="tag1"
            type="text"
            placeholder="ex.WIFI"
            value={tag1}
            onChange={(e) => {
              settag1(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>2</Form.Label>
          <FormControl
            id="tag2"
            type="text"
            placeholder="ex."
            value={tag2}
            onChange={(e) => {
              settag2(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>3</Form.Label>
          <FormControl
            id="tag3"
            type="text"
            placeholder="ex."
            value={tag3}
            onChange={(e) => {
              settag3(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            as="textarea"
            id="article"
            type="text"
            placeholder=""
            value={article}
            onChange={(e) => {
              setarticle(e.target.value);
            }}
          />
        </Form.Group>

        <Button
          className="w-100 buttonStyle"
          variant="outline-primary"
          onClick={async (e) => {
            await onSubmitF(e);
            await onSubmit();
            await onHide();
          }}
        >
          
        </Button>
      </Form>
    </Modal>
  );
}

export default Create;
