import React from 'react';
import ReactDOM from 'react-dom';
import Axios from 'axios';//POST
// react-bootstrap CSS
import '../index.css';
import {Container,Row,Col,Stack,
 Form,
 FormControl,
 Button } from 'react-bootstrap';
 import FMemberIcon from './MemberIcon.js';

//
//
class CUsersStoreComment extends React.Component{
  // 
    constructor(props) {
      super(props);
      this.state={
        FoxResponseDATA:""
      }
      this.authShowComment=this.authShowComment.bind(this); //changePercent
    }
    componentDidMount(){
        this.authShowComment();}
    authShowComment = async()=>{  
      try{
        const id = localStorage.getItem("id");
        // console.log(id);
        const foxResponse= await Axios.post('http://localhost:3001/showUserComment',{
          headers:{"x-access-token":localStorage.getItem("token"),},
          id:id,
        })
          let foxResponseDATA = foxResponse.data;
          this.setState({
             FoxResponseDATA:foxResponseDATA
          });
          // console.log(foxResponseDATA);

      }catch(err){console.log(err);}

          

    }
    
    //ResponsemapArray
    //render *Object.entries
        renderObject(){
	        	return Object.entries(this.state.FoxResponseDATA).map(([key, value], i) => {
	          	return (

                  <Container fluid className="CommentLine ">
            <Row id="commentUserInfo" >
              {/* + */}
              <Col lg={4} className="text-center"> 
                <Col lg={5} className="userIcon w-100"><FMemberIcon/></Col>
                <Col className="commenterName "><h6>{localStorage.getItem('name')}</h6></Col>
              </Col>  

              <Col lg={8} className="CommentStoreInfo">
                  <Stack>
                    <Col><h6>{value.time} </h6></Col>
                    <Col><h6>{value.name}</h6></Col>
                    <Col><h6>:{value.score}</h6></Col>
                    <div></div> 
                  </Stack>
              </Col>
            </Row>
            {/*  */}
                      <Row className="mt-3">
                        <Col lg={4}></Col>
                        <Col className="commentText">:</Col>
                      </Row>
                      <Row className="">
                        <Col lg={4}></Col>
                        <Col className="commentText">{value.comment}</Col>
                      </Row>
                      <Row className="mt-3">
                        <Col lg={4}></Col>
                        <Col lg={8} ><img src="https://picsum.photos/id/102/250/150"/></Col>
                      </Row>
                     </Container >                  
	          		)
	          	})
	       }
        render () {
          if (localStorage.getItem('id')){
            //this.state.FoxResponseDATA=0 map render
            return (
                <div>
                  {this.renderObject()}
                </div>
              );
          }else{
            return(
            <Container fluid >
            <Row id="commentUserInfo" >
              {/* + */}
              <Col lg={4} > 
                <Col lg={5} className="userIcon w-100"><FMemberIcon/></Col>
                <Col className="commenterName"></Col>
              </Col>  

              <Col lg={8} className="CommentStoreInfo">
                  <Stack>
                    <Col></Col>
                    <Col></Col>
                    <Col></Col>
                  </Stack>
              </Col>
            </Row>
            {/*  */}
                      <Row>
                        <Col lg={4}></Col>
                                  <Col className="commentText">:</Col>
                      </Row>
          
                      <Row>
                        <Col lg={4}></Col>
                        <Col lg={8} ></Col>
                      </Row>
                     </Container >   
            )
          }

        }
}

export default CUsersStoreComment;