import React from "react";
import { Button, Form, FormControl, Image, Modal } from "react-bootstrap";
import { useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";

//
// setnewImg
// 
// 
//newImg()
function Update({
  onHide,
  show,
  id,
  name,
  address,
  phone,
  city,
  foodtype,
  cost,
  f1,
  f2,
  s1,
  s2,
  tag1,
  tag2,
  tag3,
  article,
  img,
  status,
}) {
  const [ostatus, setstatus] = useState(status); //status
  const [oname, setname] = useState(name); //oname->oldname
  const [oaddress, setaddress] = useState(address);
  const [ophone, setphone] = useState(phone);
  const [ocity, setcity] = useState(city);
  const [ocost, setcost] = useState(cost);
  const [oft, setft] = useState(foodtype);
  const [of1, setf1] = useState(f1);
  const [of2, setf2] = useState(f2);
  const [os1, sets1] = useState(s1);
  const [os2, sets2] = useState(s2);

  const [otag1, settag1] = useState(tag1);
  const [otag2, settag2] = useState(tag2);
  const [otag3, settag3] = useState(tag3);
  const [oarticle, setarticle] = useState(article);
  const [files, setFiles] = useState([]); //FileList{0: File, length: 1}//1.post2.

  //
  let newImg = img;
  const inputImg = (e) => {
    setFiles(e.target.files);
    let blah = document.getElementById("blah");
    let [file] = e.target.files;
    if (file) {
      blah.src = URL.createObjectURL(file);
    }
    //[file]createurlid=blah
  };

  // put()
  const onSubmit = async () => {
    await axios
      .put("http://localhost:3001/sdata/update", {
        name: oname,
        address: oaddress,
        phone: ophone,
        city: ocity,
        ft: oft,
        cost: ocost,
        time1: of1 + "-" + of2,
        time2: os1 + "-" + os2,
        tag1: otag1,
        tag2: otag2,
        tag3: otag3,
        article: oarticle,
        img: newImg,
        id: id,
        status: ostatus,
      })
      .then((res) => {
        toast.success("");
        window.location.reload();
      })
      .catch((error) => {
        console.error(error);
      });
  };
  //post
  const onSubmitF = async (e) => {
    e.preventDefault();
    const data = new FormData();

    for (let i = 0; i < files.length; i++) {
      data.append("file", files[i]);
    } //data[] key'file' ,post

    await axios //
      .post("http://localhost:3001/sdata/picupdate", data)
      .then((response) => {
        console.log(response.data[0].filename); //
        newImg = response.data[0].filename;
      })
      .then(() => {})
      .catch((e) => {
        toast.error("Upload Error");
      });
  };

  // 
  const ondelete = (id) => {
    let temp = window.confirm("");
    if (temp == true) {
      axios
        .delete(`http://localhost:3001/sdata/delete/${id}`)
        .then(() => {
          toast.success("");
          window.location.reload();
        })
        .catch((err) => {
          alert(err);
        });
    } else {
      return;
    }
  };

  return (
    <Modal
      className="model "
      size="lg"
      aria-labelledby="contained-modal-title-vcenter "
      centered
      {...{ onHide, show }}
    >
      <Form className=" mx-auto frame my-5">
        <div>
          <h3 className="line">
            
            <button
              className="buttonStyle"
              style={{
                fontSize: "15px",
                float: "right",
                borderRadius: "3px 3px 3px 3px",
                // marginTop: "-20px",
                // marginRight: "-10px",
              }}
              // className="btn-close"
            >
              CLOSE
            </button>
          </h3>
        </div>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="name"
            type="text"
            placeholder="name"
            value={oname}
            onChange={(e) => {
              setname(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="ft"
            id="ft"
            value={ostatus}
            onChange={(e) => {
              setstatus(e.target.value);
            }}
          >
            <option value={1}></option>
            <option value={0}></option>
          </Form.Select>
        </Form.Group>
        <Form.Label></Form.Label>
        <FormControl
          id="impInp"
          type="file"
          onChange={(e) => inputImg(e)}
          className="form-control"
          multiple
          accept="image/gif, image/jpeg, image/png"
        ></FormControl>
        {/* </div> */}
        <Image
          id="blah"
          src={img && `http://localhost:3001/${img}`}
          thumbnail
        />

        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="address"
            type="text"
            placeholder="address"
            value={oaddress}
            onChange={(e) => {
              setaddress(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="phone"
            type="text"
            placeholder="phone"
            value={ophone}
            onChange={(e) => {
              setphone(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="city"
            id="city"
            value={ocity}
            onChange={(e) => {
              setcity(e.target.value);
            }}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <Form.Select
            aria-label="Default select example"
            name="ft"
            id="ft"
            value={oft}
            onChange={(e) => {
              setft(e.target.value);
            }}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            id="cost"
            type="text"
            placeholder="$"
            value={ocost}
            onChange={(e) => {
              setcost(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>1</Form.Label>
          <FormControl
            style={{ marginBottom: "5px" }}
            type="time"
            value={of1}
            onChange={(e) => {
              setf1(e.target.value);
            }}
          />
          <FormControl
            type="time"
            value={of2}
            onChange={(e) => {
              setf2(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>2</Form.Label>
          <FormControl
            style={{ marginBottom: "5px" }}
            type="time"
            value={os1}
            onChange={(e) => {
              sets1(e.target.value);
            }}
          />
          <FormControl
            type="time"
            value={os2}
            onChange={(e) => {
              sets2(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>1</Form.Label>
          <FormControl
            id="tag1"
            type="text"
            placeholder="ex.WIFI"
            value={otag1}
            onChange={(e) => {
              settag1(e.target.value);
            }}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>2</Form.Label>
          <FormControl
            id="tag2"
            type="text"
            placeholder="ex."
            value={otag2}
            onChange={(e) => {
              settag2(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>3</Form.Label>
          <FormControl
            id="tag3"
            type="text"
            placeholder="ex."
            value={otag3}
            onChange={(e) => {
              settag3(e.target.value);
            }}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label></Form.Label>
          <FormControl
            as="textarea"
            id="article"
            type="text"
            placeholder=""
            value={oarticle}
            onChange={(e) => {
              setarticle(e.target.value);
            }}
          />
        </Form.Group>

        <Button
          style={{
            float: "left",
            backgroundColor: "rgb(160, 87, 87)",
          }}
          id="deleteButton"
          className="w-10 buttonStyle"
          variant="outline-primary"
          onClick={() => {
            ondelete(id);
            onHide();
          }}
        >
          
        </Button>

        <Button
          style={{
            float: "right",
          }}
          className="w-50 buttonStyle"
          variant="outline-primary"
          onClick={async (e) => {
            await onSubmitF(e);
            await onSubmit();
            await onHide();
          }}
        >
          
        </Button>
      </Form>

      <Button variant="outline-secondary" onClick={onHide}>
        Close
      </Button>
    </Modal>
  );
}

export default Update;
