import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {  Row, Col, Container } from 'react-bootstrap';

// 
import Woman from './icon/woman.png';
import Male from './icon/male.png';
import Female from './icon/female.png';
// 
import Axios from 'axios';
// 
// import { FaStar } from 'react-icons/fa';

import { css, ThemeProvider } from "styled-components";
import { base, Star } from "grommet-icons";
import { deepMerge } from "grommet-icons/utils";




const starDesign = {
    marign: "20px",

}

function Share({ data }) {

    const [ReviewList, setList] = useState([]);



    const customColorTheme = deepMerge(base, {
        icon: {
            extend: css`
        ${(props) =>
                    props.color === "golden" &&
                    `
        fill: #FFDC35;
      `}
      `
        }
    });



    useEffect(() => {
        Axios.get('http://localhost:3001/api/get').then((response) => {
            setList(response.data);
            console.log(response.data)
        });
    }, []);


    return (
        <div>
            &nbsp;
            <Container className=" d-flex flex-column "  >

                <Row className="border  border-warning p-2  " >
                    <Col className="border border-warning p-1 bg-warning  " xs={3} xl={1}  >

                        <img src={Male} className='userphoto' />

                    </Col>
                    <Col xs={9} xl={11} >
                        <div className="text-dark text-align"   >

                            ~~
                            <br />
                            <br />
                            <ThemeProvider theme={customColorTheme}>
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                            </ThemeProvider>
                        </div>
                    </Col>

                </Row>


                <Row className="border border-warning p-2 " >
                    <Col className="border border-warning p-1 bg-warning  " xs={3} xl={1}  >

                        <img src={Woman} className='userphoto' />

                    </Col>
                    <Col xs={9} xl={11} >
                        <div className="text-dark text-align"   >
                            
                            <br />
                            <ThemeProvider theme={customColorTheme}>
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                            </ThemeProvider>
                        </div>
                    </Col>

                </Row>

                <Row className="border border-warning p-2 " >
                    <Col className="border border-warning p-1 bg-warning  " xs={3} xl={1}  >

                        <img src={Male} className='userphoto' />

                    </Col>
                    <Col xs={9} xl={11} >
                        <div className="text-dark text-align"   >
                            
                            <br />
                            <br />
                            <ThemeProvider theme={customColorTheme}>
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                            </ThemeProvider>


                        </div>
                    </Col>

                </Row>


                <Row className="border border-warning p-2 " >
                    <Col className="border border-warning p-1 bg-warning  " xs={3} xl={1}  >

                        <img src={Female} className='userphoto' />

                    </Col>
                    <Col xs={9} xl={11} >
                        <div className="text-dark text-align"   >
                            
                            <br />
                            <br />
                            <ThemeProvider theme={customColorTheme}>
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                                <Star color="golden" size="medium" />
                            </ThemeProvider>
                        </div>
                    </Col>

                </Row>
                {/*  */}


                <Row className="border border-warning p-2 " >
                    <Col className="border border-warning p-1 bg-warning  " xs={3} xl={1}  >

                        <img src={Female} className='userphoto' />

                    </Col>
                    <Col xs={9} xl={11} >
                        {ReviewList.map((val) => {
                            return <div className="text-dark text-align"   >
                                {val.Review}
                            </div>;
                        })}


                        <br />
                        <ThemeProvider theme={customColorTheme}>
                            <Star color="golden" size="medium" />
                            <Star color="golden" size="medium" />
                            <Star color="golden" size="medium" />
                            <Star color="golden" size="medium" />
                        </ThemeProvider>






                    </Col>

                </Row>



            </Container>




        </div>

    )
}

export default Share;


