import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

//slice
// 1.(stusus)()
// 2.storereducer(=slicename)
// 3.indexcommentFetch

//slice for 
//data:axios 

//
const initialState = {
  data: [],
  status: [], //fetch(success,padding,error)
};

//fetch data
export const commentFetch = createAsyncThunk("comment/commentFetch", async () => {
  try {
    const rep = await axios.get("http://localhost:3001/comment");
    return rep.data;
  } catch (error) {
    console.log(error);
  }
});

const commentSlice = createSlice({
  name: "comment",
  initialState,
  reducers: {},
  extraReducers: {
    [commentFetch.padding]: (state, action) => {
      state.status = "padding";
    },
    [commentFetch.fulfilled]: (state, action) => {
      state.data = action.payload;
      state.status = "success";
    },
    [commentFetch.rejected]: (state, action) => {
      state.status = "rejected";
    },
  },
});

// Action creators are generated for each case reducer function
export const {} = commentSlice.actions;

export default commentSlice.reducer;
